*{
    padding: 0;
    margin: 0;
}

.betting-section p{
    text-align: center;
    font-size: 2.25rem; 
    line-height: 2.5rem;
    color: red;
}

.betting-section > .horse-choice > .first-horse > #first-horse{
    border: 2px solid #fa93a8;
    border-radius: 30px;
    margin: 10px 0 10px 0;
    padding: 10px 0 10px 0;
    color: black;
    font-size: large;
    width: 10%;
    background-color: white;
    transition-duration: 0.4s;
}

.betting-section > .horse-choice > .first-horse > #first-horse:hover{
    background-color: #fa93a8;
    color: white;
    cursor: pointer;
}

